.slider{
    width: 100%;
    height: 600px;
    overflow: hidden;
    position: relative;
}

.slider .slide{
    float: left;    
}

.slider .slide > img{
    width:100%;
    height:auto;
}

.slider .slider-wrapper{
    
}

.slider .left{
    position: absolute;
    top: 40%;
    left: 5%;
    font-size: 55px;
    font-weight: bold;
    color:#fff;
    font-family: monospace;
    cursor: pointer;
}

.slider .right{
    position: absolute;
    top: 40%;
    right: 5%;
    font-size: 55px;
    font-weight: bold;
    color:#fff;
    font-family: monospace;
    cursor: pointer;
}

@media only screen and (max-width: 1250px) {
    .slider{
      height: auto;
    }
  }