@import url('https://fonts.googleapis.com/css?family=Great+Vibes|Raleway:300,400,700');

body{
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}

h1{
  font-size: 7vw;
  font-weight: 700;
}

h2{
  font-size: 5vw;
  font-weight: 700;
}

h3{
  font-size: 5vw;
  font-weight: 300;
}

h4{
  font-size: 3vw;
  font-weight: 700;
}

h5{
  font-size: 2vw;
  font-weight: 700;
}

h6{
  font-size: 1.5vw;
  font-weight: 700;
}

.es-it-fnt{
  font-family: 'Great Vibes', cursive;
  font-weight: 400;
}

.eshop-hd-lt{
  font-weight: 300;
}

.eshop-hd{
  font-weight: 700;
}

.App {
  text-align: center;
}

.cboth{
  clear: both;
}

.loading{
  background:#333;
  color:#fff;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.header {
  background-color: #282c34;
  height: auto;
  color: white;
}
.header a.logo{
  width: 30%;
    float: left;
    display: block;
    padding: 30px;    
    padding-left: 2%;
    padding-right: 1%;
}

 .header nav{
    display: block;
    width: 65%;
    padding: 30px;    
    padding-left: 0px;
    padding-right: 2%;
    margin-left: auto;
}

.header ul{
  padding: 0px;
    margin: 0px;
    list-style: none;
    text-align: right;    
}

.header ul li{
  display: inline-block;
  padding-left: 12px;
  padding-right: 12px;
  border: 1px solid #fff;
  margin-left: 1%;
  margin-right:1%;
  cursor: pointer;
  transition: .836s;
}

.header ul li:hover{
  background:rgba(255,255,255,.9);
  color: #000;
}

.header ul li:hover a{
  color: #000;
}

.header ul li a{
  color:#fff;
  line-height:32px;
  padding-bottom: 2px;
  text-decoration: none;
  display: inline-block;
}

/******Style*******/

.cboth{
  clear: both;
}

.es-col2{
  width:50%;
  float:left;
  transition: .636s;
}

.es-col2.lft-pd1{
  width:49.5%;
  float:left;
  margin-right: .5%;
  transition: .636s;
}

.es-col2.rt-pd1{
  width:49.5%;
  float:left;
  margin-left: .5%;
  transition: .636s;
}

.es-col4{
  width:25%;
  float:left;
  transition: .636s;
}

.es-col4-pd1{
  width:24.5%;
  float:left;
  margin-left: .25%;
  margin-right: .25%;
  transition: .636s;
}

.left-from-inner-slide{
  position: absolute;
  transition: .836s;
  right:80%;
  left:auto;
}

.right-from-inner-slide{
  position: absolute;
  transition: .836s;
  left:80%;
  right:auto;
}

/**Home**/

.hm-top{
  margin-top: 12px;
}

.hm-top > div.es-col2{
  position: relative;
  height: 1200px;
  overflow: hidden;
}

.hm-top > div > div > p{
  background: rgba(255,255,255,.5);
  opacity: .7;
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding-top: 40%;
  font-size: 12vw;
  color: #333;
}

.hm-top > div > div > p a:active,
.hm-top > div > div > p a:focus,
.hm-top > div > div > p a{
  text-decoration: none;
  color: #333;
}

.es-men{
  background: url('../../assets/images/jeremy-beadle-129624-unsplash.jpg');
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.es-women{
  background: url('../../assets/images/ahmed-carter-433915-unsplash.jpg');
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.hm-top2{
  margin-top: 12px;
}

.hm-top2 .es-full{
  background: url('../../assets/images/tamara-bellis-388531-unsplash.jpg');
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  background-size: cover;
}

.hm-top2 .es-full p{
  padding-top: 10%;
  padding-bottom: 10%;
  font-size: 8vw;
  color: #f57325;
  margin: 0px;
  background: rgba(0,0,0,.5);  
}

.recent{
  margin-bottom:12px; 
}

.recent .es-col4-pd1{
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.recent .es-col4-pd1 img{
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  height: 100%;
}

.recent .es-col4-pd1 .inner-txt{
  transition: .835s;
  transform:scale(0);
  background: rgba(255,255,255,.5);
  cursor: pointer;
}

.recent .es-col4-pd1:hover > .inner-txt{
  transform:scale(1);
}

.recent .es-col4-pd1 .inner-txt a,
.recent .es-col4-pd1 .inner-txt a:hover,
.recent .es-col4-pd1 .inner-txt a:active,
.recent .es-col4-pd1 .inner-txt a:focus{
  color:#000;
  text-decoration: none;
}

/** Shop **/

.shop{
  margin-top: 30px;
}

.shop a{
  text-decoration: none;
  color: #333;
}

.shop ul{
  display: block;
  padding: 0px;
  margin: 0px;
}

.shop li{
  width: 24%;
  float: left;
  margin-left: .48%;
  margin-right: .48%;
  list-style: none;
  transition: .636s;
  transform:scale(1);
}

.shop .img-holder{
  width:100%;
  overflow: hidden;
  display: block;
  transition: .636s;
}

.shop .des{
  padding: 4px;
  width: 100%;
  transition: .636s;
  height:60px;
}

.shop .pd-title{
  padding: 4px;
  width: 100%;
  height: 62px;
  margin-top: 12px;
    margin-bottom: 12px;
    transition: .636s;
}

/**Product Page**/
.product{
  margin-top:20px;
  margin-bottom: 20px;
  transition: .636s;
}

.product .left{
  width: 56%;
  margin-right: 1%;
  float:left;
  transition: .636s;
  transform:scale(1);
}

.product .innerLeft{
  width: 20%;
  margin-right: 1%;
  margin-left: 1%;
  float:left;
  cursor: pointer;
  transition: .636s;
  transform:scale(1);
}

.product .innerLeft .img-holder{
  margin-bottom: 8px;
  display: block;
  overflow: hidden;
  transition: .636s;
  transform:scale(1);
}
.product .innerRight{
  width: 78%;
  float:left;
  overflow: hidden;
  transition: .836s;
  transform:scale(1);
}


.product .right{
  width: 41%;
  float:right;
  transform:scale(1);
  padding-left: 1%;
  padding-right: 1%;
}

.txt-shad{
  display: inline-block;
  background: linear-gradient(to right, rgba(255,255,255,.8), rgba(255,255,255,1));
  width: 32px;
  height: 20px;
  margin-left: -30px;
}

.footer{
  background: #333;
  color:#fff;
  padding: 12px;
  padding-left: 30px;
    padding-right: 30px;
}

.footer .dv-by{
  color:#fff;
  float:left;
  width: 50%;
}

.footer ul{
  list-style: none;
  float:left;
  width: 50%;
  margin: 0px;
  padding: 0px;
  text-align: right;
}

.footer ul li{
  display: inline-block;
  margin-left: 12px;
    margin-right: 12px;
    border-bottom: 2px solid #fff;
}

.footer ul li a{
  color: #fff;
  text-decoration: none;
  line-height: 51px;
}


.fade-in {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 1s;
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}


@media only screen and (max-width: 1050px) {
  .hm-top > div.es-col2{
    height: 740px;
  }

  .shop li,.es-col4-pd1{
    width:49%;
    float:left;
    margin-left: .5%;
    margin-right: .5%;
    text-align: center;
  }

  .recent .es-col4-pd1{
    height:250px;    
  }
  .recent .es-col4-pd1 .inner-txt{
    margin-top: 18%;
  }

  .shop .pd-title{
    height: 44px;
  }

  .product .innerLeft,
  .product .innerRight{
    width: 98%;
    text-align: center;
  }

  .product .innerLeft .img-holder{
    display: inline-block;
    margin-left: .5%;
    margin-right: .5%;
    width: 24%;
  }

}

@media only screen and (max-width: 800px) {
  .hm-top > div.es-col2{
    height: 400px;
  }
}

@media only screen and (max-width: 700px) {
  .shop li,.es-col4-pd1{
    width:99%;
    float:left;
    margin-left: .5%;
    margin-right: .5%;
  }
  .pd-title{
    font-size: 5vw;
    height: auto;
  }
  .product .left,
  .product .right{
    width: 98%;
    float: none;
  }
}

@media only screen and (max-width: 500px) {
  .hm-top > div.es-col2{
    height: 340px;
    width: 100%;
  }
  .hm-top > div > div > p{
    padding-top: 30%;
  }
}